<template>
  <div class="home">
    <div class="home-nav">
      <p class="title">{{ APPNAME }}</p>
      <Icon type="md-exit" size="20" title="退出登陆" @click="logout" class="pointer" />
    </div>
    <div class="home-content">
      <Menu :active-name="activeMenu" width="170px" class="home-menu">
        <MenuItem
          v-for="(item, index) in routeList"
          :key="index"
          :name="item.name"
          :to="`/${item.name}`"
        >
          <Icon :type="item.icon" size="20" />
          <Badge :count="badge[item.name] || 0" overflow-count="99" class="menu-badge">
            {{ item.title }}
          </Badge>
        </MenuItem>
      </Menu>
      <div class="home-container">
        <router-view v-slot="{ Component }">
          <keep-alive>
            <component :is="Component" />
          </keep-alive>
        </router-view>
      </div>
      <div class="qrcode-box">
        <p class="title internet" v-show="internet">手机打开（内网）</p>
        <a :href="internet" target="_blank">
          <div class="qrcode" ref="qrcode1"></div>
        </a>
        <p class="title" v-show="httpUrl">手机打开（外网）</p>
        <a :href="httpUrl" target="_blank">
          <div class="qrcode" ref="qrcode2"></div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { Menu, MenuItem } from "view-ui-plus";
import QRCode from "qrcodejs2";
import { getDraftCount } from "@/axios";

export default {
  name: "home",
  components: { Menu, MenuItem },
  data() {
    return {
      APPNAME: window.APPNAME,
      activeMenu: this.$route.name,
      routeList: [
        { title: "出库管理", name: "cashierMng", icon: "ios-cart" },
        { title: "商品管理", name: "goodMng", icon: "ios-filing" },
        { title: "订单管理", name: "orderMng", icon: "ios-clipboard" },
        { title: "客户管理", name: "userMng", icon: "ios-people" },
        { title: "特价管理", name: "specialMng", icon: "ios-pricetag" },
        { title: "统计分析", name: "statistiAnalysis", icon: "ios-stats" },
      ],
      badge: {},
      httpUrl: "",
      internet: "",
    };
  },
  watch: {
    $route: {
      deep: true,
      immediate: true,
      handler({ name }) {
        this.activeMenu = name;
      }
    },
  },
  methods: {
    logout() {
      sessionStorage.clear();
      this.$router.replace("/login?from=logout");
      window.socket && window.socket.close();
      window.socket = null;
    },
    getHref(url) {
      const token = sessionStorage.getItem("token") || localStorage.getItem("token");
      const webConfig = JSON.parse(sessionStorage.getItem("webConfig") || "{}");
      if (webConfig.autoLogin) {
        return `${url}/#/mobileHome?token=${token}`;
      } else {
        return `${url}/#/login`;
      }
    },
    createQrcode() {
      const website = JSON.parse(sessionStorage.getItem("website") || "{}");
      if (website.internet) {
        this.$refs.qrcode1.innerHTML = "";
        this.internet = this.getHref(website.internet);
        new QRCode(this.$refs.qrcode1, {
          text: this.internet,
          width: 150,
          height: 150,
          colorDark: "#999",
        });
      }
      if (website.httpUrl) {
        this.$refs.qrcode2.innerHTML = "";
        this.httpUrl = this.getHref(website.httpUrl);
        new QRCode(this.$refs.qrcode2, {
          text: this.httpUrl,
          width: 150,
          height: 150,
          colorDark: "#555",
        });
      }
    },
    async getDraftCount() {
      try {
        const { returnCode, data } = await getDraftCount() || {};
        if (returnCode === 0) {
          this.badge.orderMng = data || 0;
        }
      } catch (err) {
        this.$Message.error(`获取草稿数量失败：${err.message || err}`);
      }
    },
  },
  mounted() {
    this.createQrcode();
    this.getDraftCount();
    this.$EventBus.$on("saveOrder", this.getDraftCount);
  },
  beforeUnmount() {
    this.$EventBus.$off("saveOrder", this.getDraftCount);
  },
};
</script>

<style lang="less" scoped>
.home {
  height: 100vh;
  overflow: hidden;
  & .home-nav {
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-shadow: 0 3px 3px #dddddd;
    z-index: 1;
    & .title {
      font-size: 20px;
      font-weight: bold;
    }
  }
  & .home-content {
    height: calc(100% - 45px);
    display: flex;
    & .home-menu {
      border-radius: 0 5px 5px 0;
      margin-top: 1px;
      min-width: 160px;
      .menu-badge {
        :deep(.ivu-badge-count) {
          right: -30px;
          top: 0;
        }
      }
    }
    & .home-container {
      flex: 1;
      overflow-x: auto;
      overflow-y: hidden;
      & div {
        min-width: 900px;
      }
    }
    & .qrcode-box {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 170px;
      padding: 10px;
      z-index: 2;
      .title {
        text-align: center;
        font-weight: 600;
        margin: 5px;
        color: #555;
      }
      .internet {
        color: #999;
      }
    }
  }
}
</style>
